:root {
  --unit: 8px;

  /**
   * Colors.
   */

  --primary-color: #01d277;
  --secondary-color: #081c24;
  --secondary-color-ligth: #213339;

  /**
   * Box shadows.
   */

  --box-shadow-base: 0 2px 4px rgba(0,0,0,0.5), 0 8px 16px rgba(0,0,0,0.5);

  /**
   * z-indexes.
   */

  --z-index-input-search-panel: 20;
}

* {
  box-sizing: border-box;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    line-height: 1;
    background-color: #081c24;
    background-color: var(--secondary-color);
    color: white;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

.container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.main {
    background-color: var(--secondary-color);
}

.nav {
    width: 100%;
    padding: calc(var(--unit) * 3);
}

body, html {
  height: 100%;
  min-height: 100%;
}
.bg {
  /* The image used */
  background-color: #213339;
  -webkit-align-content: center;
          align-content: center;
  /* #081c24 */
  /* #213339 */

  /* Full height */
  height: 100%;
  min-height: 100%;
  width: 100%;
  min-width: 100%;

  /* background-image: url("../images/ev.png");
  opacity: 0.9; */

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
  color: white;
}

